<template>
  <div class="container">
    <h3>DASHBOARD</h3>

    <v-card class="mx-auto" max-width="300" tile>
      <v-list flat>
        <v-subheader>EMPLEADOS</v-subheader>
        <v-list-item-group v-model="item" color="primary">
          <v-list-item v-for="(item, i) in employees" :key="i">
            <v-btn :to="{ name: 'view-employee', params:{employee_id:item.id} }" class="mr-10" fab dark small color="cyan">
              <v-icon dark>edit</v-icon>
            </v-btn>
            <v-list-item-content>
              <v-list-item-title v-text="item.name"></v-list-item-title>
              <v-list-item-subtitle v-text="item.area"></v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
    <v-btn :to="{ name: 'new-employee' }" color="pink" dark small absolute bottom right fab>
      <v-icon>add</v-icon>
    </v-btn>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employees: [],
      item: 1,
    };
  },
  created() {
    db.collection("employees")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(row => {
          console.log(row.data());
          this.employees.push({
            uuid: row.id,
            id: row.data().id,
            name: row.data().name,
            area: row.data().area
          });
        });
      });
  }
};
</script>